@use '../../global' as g;
@use '../../component';
@use '../../layout';
@use '../../foundation';
/*!
page > common > common
------------------------------
*/
#wrapper {
    position: relative;
    overflow: hidden;
}

.pc {
    display: none;
}
body.overflow {
    overflow: hidden;
}
@include g.mq('md') {
    .sp {
        display: none;
    }
    html {
        overflow-y: overlay;
        overflow-y: auto;
        /* Fix not show on Firefox */
        scrollbar-color: #2aa3e4 #eee;
        scrollbar-width: thin;
    }
    html.overflow {
        overflow-y: hidden;
    }
    /* For Chrome and other browsers except Firefox */
    body::-webkit-scrollbar {
        width: 0.5em;
        background-color: #eee;
    }
    body::-webkit-scrollbar-thumb {
        background-color: #2aa3e4;
    }
}
